import jjGoodsItem from '../../components/jj-goods-item.vue'
export default {
	data() {
		return {
			type:''
		}
	},
	components:{
		jjGoodsItem
	},
	onLoad(e) {
		this.type = e.type;
	},
	methods: {
		goodsInfo(){
			var _this = this;
